/* General HTML stuff */

@import url(https://fonts.googleapis.com/css?family=Lato:400,300,100);

* {
  font-family: 'Lato';
}

html {
  height: 100%;
  background-size: contain;
  background: -webkit-gradient(linear, left top, left bottom, from(#00F260), to(#0575E6));
  background: -webkit-linear-gradient(#00F260, #0575E6);
  background: -o-linear-gradient(#00F260, #0575E6);
  background: linear-gradient(#00F260, #0575E6);
  background-repeat:no-repeat;
}

a {
  text-decoration: none;
  color: inherit;

}

a:hover {
  color: #0575E6;
}

p {
  line-height: 1.6;
}

body {
  height: 1000px;
}

img {
  max-width: 100%;
}

/* Home */
.profile {
  text-align: center;
  color: white;
  padding-top: 10px;
  opacity: 1;
  visibility: visible; 
}

/* Arrow pulse */
.pulse {
  display: inline-block;
  -webkit-animation: pulse 2s infinite linear;
  animation: pulse 2s infinite linear;
}

@-webkit-keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}
@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}


/* Tablet and Desktop */
@media screen and (min-width: 490px) {
  .profile {
    padding-top: 50px;
  }
}

#first {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

#second {
  -webkit-animation-delay: 1.25s;
          animation-delay: 1.25s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

#profileImage {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

#jobs {
  -webkit-animation-delay: 1.75s;
          animation-delay: 1.75s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.viewportCover {
  width: 100%;
  min-height: 100vh;
}


/* About Me */
#aboutMe {
  text-align: center;
  margin: 100px 20px 100px 20px;
}

/*margin: 100px 200px 100px 200px;*/

/* Desktop */
@media screen and (min-width: 960px) {
  #aboutMe {
    text-align: center;
    margin: 100px 200px 100px 200px;
  }
}

#arrowIcon {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

/* Resume */
#resume {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  /* Add back in if you feel like it
  cursor: -moz-zoom-in; 
  cursor: -webkit-zoom-in; 
  cursor: zoom-in;
  */
}

@media screen and (min-width: 400px) and (max-width: 960px){
  #resume {
    margin-left: -1.5rem;
    margin-right: 0;
    width: 100%;
  }
}

#printableText {
  text-align: center;
  font-size: 12pt;
}


/* Projects */
.card {
  padding: 10px 10px 10px 20px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  width: 300px;
  height: 350px;
  position: relative;
}

.card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
    padding: 25px 16px;
}

.bottomLeftIcon:hover {
  color: blue;
}

.higherCard {
  padding: 15px 10px 15px 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#allCards {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

/* Tablet */
@media screen and (min-width: 660px){
  .higherCard {
    width: 50%;
  }
}

/* Desktop */
@media screen and (min-width: 961px) {
  #allCards {
    width: 960px;
    margin: 0 auto;
  }
}


/* Footer */
#footer {
  background: -webkit-gradient(linear, left top, left bottom, from(#00F260), to(#0575E6));
  background: -webkit-linear-gradient(#00F260, #0575E6);
  background: -o-linear-gradient(#00F260, #0575E6);
  background: linear-gradient(#00F260, #0575E6);
  width: 100%;
  position: relative;
  margin: 0 -0.01rem;
  padding: .25rem 0.01rem;
}

.footerContent {
  display: inline-block;
  vertical-align: middle;
  color: white;
  text-align: center;
  width: 100%;
}


